.appContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  min-height: 100vh;
  font-weight: 800;
  font-size: 15px;
  line-height: 18px;
  color: black;
}
.appContentContainer {
  display: flex;
  flex-direction: column;
  margin-top: 150px;
  background-color: white;
  width: 95%;
  border-radius: 50px;
  margin-bottom: 100px;
}
.appTitle {
  display: flex;
  margin: 20px auto 0px;
  font-size: 30px;
}
